#login{
  background: teal;
  position: absolute;
  width: 100%;
  height: 100%;
}
div#grid{
  position: fixed;
  align-items: center;
  width: 100%;
  height: 100%;
}
.submit{
  background: #fff !important;
  color: teal !important;
}
.greetings{
  color: #fff;
}
.greetings> span {
  font-size: 18px;
}
#sidebar{
  position: fixed;
  display: flex;
  height: 100vh;
  flex-direction: column;
  background: whitesmoke;
  border-right: 3px solid #e3e3e3;
}

.user-name{
  margin: 8px;
  font-weight: 600;
}

.hide-menubar{
  width: 40px;
  height: 40px;
  display: flex !important;
  border-radius: 100% !important;
  margin-top: 10px !important;
  margin-left: 10px !important;
  align-items: center;
  position: fixed;
  z-index: 100;
  justify-content: center;
}

.avatar-box {
  padding: 20px 10px 20px 10px;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.above-table {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.actions-container{
  display: flex!important;
  justify-content: space-around;
  align-items: center;
}
#page{
  display: inline-block;
}

#sidebar-box {
  position: fixed;
  width: 15rem;
  height: 100vh;
}

#main-container {
  display: grid;
  width: 100vw;
  grid-template-columns: 15rem auto;
}

.content{
  grid-column-start: 2;
}
.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="14px"]::before {
  content: 'Normal';
  font-size: 14px !important;
}

.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="16px"]::before {
  content: 'Medium';
  font-size: 16px !important;
}

.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="20px"]::before {
  content: 'Large';
  font-size: 20px !important;
}

.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="35px"]::before {
  content: 'Huge';
  font-size: 35px !important;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.mg-0 {
  margin: 0 !important;
}
.pd-0{
  padding: 0!important;
}
.pl-5{
  padding-left: 10% !important;
}
.mb-1 {
  margin-bottom: 1%!important;
}
.mt-1 {
  margin-top: 1% !important;
}
.margin-bottom-0 {
  margin-bottom: 0 !important
}

.border-bottom-radius-0 {
  border-bottom-left-radius: 0!important;
  border-bottom-right-radius: 0!important;
}

.border-top-radius-0 {
  border-top-left-radius: 0!important;
  border-top-right-radius: 0!important;
}

#header {
  display: flex;
  justify-content:  space-between;
  align-items: center;
}

#editor {
  height: 60vh;
}

.cool {
  background: #0fc19e!important;
  color: #fff!important;
  font-size: 11pt !important;
}

.cool:hover {
  background: #0cad8d!important;
}

.cool:active {
  background: #0fc19e!important;
}

.tomato {
  background: tomato!important;
  color:#fff !important;
}

.tomato:hover {
  background: rgb(212, 67, 42)!important;
}

.tomato:active {
  background: tomato!important;
}

.title {
  color: #747475;
}

.column-center {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.hidden {
  display: none;
}

.image-upload {
  cursor: pointer;
  display: inline-block !important;
}

.image-foreground{
  display: flex;
  visibility: hidden;
  opacity: 0;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  position: absolute;
  color: #fff;
  background: rgba(0,0,0, 0.7); /*partially transparent image*/
  -webkit-transition: visibility 0s, opacity 0.2s linear;
  transition: visibility 0s, opacity 0.2s linear;
}

.image-upload:hover> .image-foreground{
  visibility: visible;
  opacity: 1;
}

.right-divider {
  border-right:2px solid lightgray;
}
